.container {
  max-width: 1256px;
  margin: 0 auto;
  padding-left: 16px;
  padding-right: 16px;
}
@media (max-width: 1100px) {
  .container {
    padding-left: 0 !important;
    padding-right: 0 !important;
    max-width: 100%;
  }
}
.container-sm {
  max-width: 1147px;
}
@media (max-width: 1100px) {
  .container-sm {
    padding-left: 0 !important;
    padding-right: 0 !important;
    max-width: 100%;
  }
}

body {
  font-family: Gilroy, sans-serif;
}

.btn {
  background: linear-gradient(89.6deg, #FFAD14 0.29%, #FFD531 99.62%);
  border: none;
  font-size: 1.15em;
  font-weight: 700;
  font-family: Gilroy;
  color: #FFFFFF;
  border-radius: 2.5em;
  padding: 1.1em 4.1em;
  transition: transform 0.3s ease-in-out;
}
.btn-lg {
  width: 100%;
}
.btn:hover {
  transform: scale(1.05);
}

.title {
  color: #083893;
  font-size: 2.2em;
  line-height: 1;
  text-align: center;
}
.title-orange {
  color: #FFD600;
}

.bg-blue {
  padding: 32px 0;
  background-color: #083893;
  background-image: url("/images/twigs.png");
  background-size: contain;
  background-position: bottom left;
}
.bg-blue .title {
  margin: 0;
  padding: 0 0 32px;
}

.slick-list {
  max-width: 1114px;
  margin: 0 auto !important;
}

.slick-arrow {
  cursor: pointer;
  width: 26px;
  height: 44px;
  position: absolute;
  border: none;
  color: transparent;
  background-color: transparent;
  top: 50%;
  transform: translateY(-50%);
}

.slick-next {
  right: 0;
  background-image: url("/images/right-arrow.svg");
}

.slick-prev {
  left: 0;
  background-image: url("/images/left-arrow.svg");
}

.list {
  overflow: hidden;
  display: flex;
}

.block {
  padding: 16px 0 0;
}

.header {
  z-index: 1000;
  background-color: #083893;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  padding: 2.8em 0px;
}
.header-container {
  display: flex;
  align-items: center;
}
.header-logo {
  max-width: 157px;
}
.header-menu {
  display: flex;
  margin-left: 6.1em;
}
@media (max-width: 1100px) {
  .header-menu {
    flex-wrap: wrap;
    gap: 20px;
    margin-left: 1em;
  }
  .header-menu-link:not(:last-child) {
    margin-right: 0 !important;
  }
}
.header-menu-link {
  color: #FFFFFF;
  text-decoration: none;
  letter-spacing: 0.08em;
  transition: color 0.3s ease-in-out;
}
.header-menu-link:not(:last-child) {
  margin-right: 4em;
}
.header-menu-link:hover {
  color: #FFD600;
}

.hero {
  background-image: url("/images/hero-twig-1.png");
  background-repeat: no-repeat;
  background-position: bottom right;
}
.hero-wrapper {
  background-color: #083893;
  background-image: url("/images/hero-twig-2.png");
  background-repeat: no-repeat;
  background-position: 85% 100%;
}
.hero-container {
  padding-top: 15em;
  padding-bottom: 8.25em;
  background-repeat: no-repeat;
  background-size: 32.1em;
  background-position: 113% 62%;
  background-origin: content-box;
}
@media (min-width: 1232px) {
  .hero-container {
    background-image: url("/images/hero.png");
  }
}
.hero-content {
  max-width: 660px;
}
.hero-title {
  margin: 0;
  font-weight: 700;
  font-size: 2.35em;
  line-height: 1.2em;
  color: #FFFFFF;
}
.hero-description {
  max-width: 480px;
  font-weight: 500;
  font-size: 1.1em;
  margin-top: 2.6em;
  margin-bottom: 2.5em;
  line-height: 1.5em;
  letter-spacing: 0.012em;
  color: #E7E7E7;
}

.doctors-item {
  width: 16.5em;
}
.doctors-item:hover .doctors-content {
  color: #FFFFFF;
  background-color: #083893;
}
.doctors-item:hover .doctors-description {
  opacity: 1;
}
.doctors-image {
  border: 2px solid #FFCA99;
  border-radius: 30px 30px 0 0;
}
.doctors-subtitle {
  margin: 0 0 1em;
}
.doctors-description {
  opacity: 0;
}
.doctors-content {
  margin-top: -2.7em;
  font-size: 1.1em;
  padding: 1.3em 1em;
  z-index: 3;
  position: relative;
  text-align: center;
  background-color: #FFFFFF;
  border: 2px solid #FFCA99;
  border-radius: 0 0 30px 30px;
  color: #21384A;
  transition: 0.3s ease-in-out;
}

.rules {
  padding-bottom: 80px;
}
.rules-list {
  display: flex;
  gap: 15px;
  justify-content: center;
  flex-wrap: wrap;
}
.rules-item {
  border: 0.845254px solid #FFCA99;
  border-radius: 12.6788px;
  width: 398px;
  padding: 13px 28px;
}
.rules-subtitle {
  color: #083893;
  margin: 0 0 10px;
}

.docs-item img {
  height: 506px;
}

.price {
  padding-bottom: 100px;
}
.price-item {
  padding: 16px 20px 16px 20px;
  background: #FFFFFF;
  border: 2px solid #D6E8FF;
  border-radius: 15.5065px;
}
.price-item:not(:last-child) {
  margin-bottom: 16px;
}
.price-item h2 {
  margin: 0;
}
.price-type {
  color: #B3B1B1;
}
.price-subtitle {
  margin: 26px 0 16px;
  color: #083893;
}
.price-description {
  font-weight: 500;
  color: #6D6767;
}
.price-price {
  display: block;
  text-align: end;
  font-weight: 700;
  font-size: 22px;
  color: #FFB800;
}

.license-item img {
  height: 506px;
}

.about {
  padding-bottom: 100px;
  position: relative;
}
.about::after {
  content: "";
  display: block;
  position: absolute;
  z-index: -1;
  height: 360px;
  left: 0;
  right: 0;
  background-color: #083893;
  top: 50%;
  transform: translateY(-55%);
}
@media (max-width: 1100px) {
  .about::after {
    background-color: transparent;
  }
}
.about-content {
  display: grid;
  grid-template-columns: 418px 1fr;
  align-items: center;
  gap: 45px;
}
@media (max-width: 1100px) {
  .about-content {
    grid-template-columns: 1fr;
  }
  .about-content > img {
    display: none;
  }
}
.about-item {
  color: #FFFFFF;
}
.about-item:not(:last-child) {
  margin-bottom: 22px;
}
@media (max-width: 1100px) {
  .about-item {
    color: #083893;
  }
}

.feedback {
  padding: 100px 0;
}
.feedback-item {
  max-width: 350px;
}
.feedback-content {
  padding: 62px 45px;
  min-height: 211px;
  background: #FFFFFF;
  border-radius: 15.5065px;
}
.feedback-writer {
  padding-top: 12px;
  gap: 18px;
  display: flex;
  color: #FFFFFF;
  align-items: center;
  position: relative;
}
.feedback-writer::after {
  content: url("/images/feedback-arrow.svg");
  width: 27px;
  height: 36px;
  display: block;
  position: absolute;
  top: -3px;
  left: 56px;
}
.feedback-subtitle {
  margin: 0 0 5px;
}

.phones {
  overflow: scroll;
  padding-bottom: 100px;
}
.phones-title {
  font-size: 27.2795px;
  line-height: 153.99%;
}
.phones table {
  text-align: left;
}
.phones table thead th {
  padding: 20px 40px 20px;
  background: #FFFFFF;
  box-shadow: 0 3.93258px 3.93258px rgba(88, 140, 236, 0.31);
}
.phones table tbody tr {
  background: #FFFFFF;
  box-shadow: 0px 3.93258px 3.93258px rgba(88, 140, 236, 0.25);
}
.phones table tbody td {
  padding: 20px 5px;
}

.form {
  padding: 28px 0 69px;
}
.form-container {
  background-image: url("/images/form.png");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: left center;
}
@media (max-width: 1100px) {
  .form-container {
    background-image: none;
  }
}
.form-wrapper {
  padding: 74px 0 34px;
  margin-left: auto;
  max-width: 370px;
}
@media (max-width: 1100px) {
  .form-wrapper {
    max-width: 400px;
    margin: 0 auto;
  }
}
.form-group {
  display: flex;
  gap: 12px;
  margin-bottom: 12px;
}
.form-input {
  background-color: #FFFFFF;
  border: none;
  border-radius: 45px;
  padding: 14px 26px;
  width: 100%;
}
.form-input::placeholder {
  color: #CDC9C9;
}
.form-date, .form-time {
  width: 100%;
  padding: 14px 28px;
  background: #FFFFFF;
  border-radius: 6.61479px;
}
.form-date::-webkit-calendar-picker-indicator, .form-time::-webkit-calendar-picker-indicator {
  transform: scale(2);
  filter: invert(71%) sepia(66%) saturate(1541%) hue-rotate(358deg) brightness(100%) contrast(106%);
}
.form-time {
  flex: 0 0 179px;
  padding: 14px 50px;
}

.footer {
  padding: 50px 0;
  background-color: #083893;
}
.footer-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: #FFFFFF;
}
@media (max-width: 1100px) {
  .footer-container {
    flex-wrap: wrap;
  }
}
.footer-logo {
  width: 226px;
}
.footer .footer-contacts {
  max-width: 311px;
}
.footer .footer-contacts > p {
  margin: 0;
  display: flex;
  align-items: flex-start;
  gap: 16px;
}
.footer .footer-contacts > p:not(:last-child) {
  margin-bottom: 16px;
}
.footer .footer-link {
  align-items: center !important;
  line-height: 1;
}
.footer .footer-link a {
  color: #FFFFFF;
  text-decoration: none;
  transition: 0.3s ease-in-out;
}
.footer .footer-link a:hover {
  color: #FFD600;
}